.span > div {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 90vw;
  text-align: center;
}

.coffee_cup {
  width: 20px;
  height: 24px;
  border: 1px rgba(255, 255, 255, 1) solid;
  border-radius: 0px 0px 5px 5px;
  position: relative;
  margin: 36px auto;
}
.coffee_cup:after,
.coffee_cup:before {
  position: absolute;
  content: '';
}
.coffee_cup:after {
  width: 5px;
  height: 12px;
  border: 1px #fff solid;
  border-left: none;
  border-radius: 0px 20px 20px 0px;
  left: 20px;
}
.coffee_cup:before {
  width: 1px;
  height: 6px;
  background-color: rgba(255, 255, 255, 1);
  top: -10px;
  left: 4px;
  box-shadow: 5px 0px 0px 0px rgba(255, 255, 255, 1),
    5px -5px 0px 0px rgba(255, 255, 255, 1),
    10px 0px 0px 0px rgba(255, 255, 255, 1);
  -webkit-animation: steam 1s linear infinite alternate;
  animation: steam 1s linear infinite alternate;
}

.loader-text {
  font-size: 1.2em;
  letter-spacing: 1px;
  line-height: 2;
}

@-webkit-keyframes steam {
  0% {
    height: 0px;
  }
  100% {
    height: 6px;
  }
}
@keyframes steam {
  0% {
    height: 0px;
  }
  100% {
    height: 6px;
  }
}

